* {
  font-family: "Roboto Condensed", sans-serif;
}

.main__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main--section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: #ced4da;
  color: #f8f9fa;
  height: 130px;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
}
.logo img {
  width: 100px;
  height: 100px;
}

.nav--bar {
  display: flex;
  justify-content: space-around;
  width: 35%;
}
.nav--bar a {
  text-decoration: none;
  color: #f8f9fa;
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #343a40;
  width: 100%;
  height: 300px;
}
footer .nav--bar--f {
  display: flex;
  justify-content: space-around;
  width: 35%;
}
footer .nav--bar--f a {
  text-decoration: underline;
  color: blue;
  font-size: 1.2rem;
}
footer .social {
  color: #f8f9fa;
}
footer .social a {
  color: blue;
  text-decoration: underline;
  font-size: 1.2rem;
}

.title {
  display: block;
  text-align: left;
  width: 80%;
  height: 100%;
}

.picture {
  display: block;
  width: 80%;
}
.picture img {
  width: 100%;
}

.openinghours {
  display: flex;
  width: 80%;
  font-size: 16px;
}

.hours {
  width: 80%;
}

.table {
  width: 80%;
}

.adress {
  width: 80%;
}

.map {
  display: block;
  width: 80%;
}
.map img {
  width: 100%;
}

.main--section--third {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

.coffies,
.coffies-two {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
}

.coffie {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 45%;
  background-color: #dadada;
}
.coffie img {
  width: 100%;
}
.coffie h3 {
  padding-left: 5px;
}
.coffie p {
  padding-left: 5px;
}

/*html2 -------------- Menu*/
h2 {
  display: block;
  margin-left: 38px;
}

.menu {
  display: flex;
  flex-direction: column;
  align-content: center;
  margin: 10px;
  width: 80%;
}

ul {
  list-style: none;
}

.nonalc {
  display: flex;
  flex-direction: row;
  flex-direction: column;
  align-content: space-between;
}

.no-alc {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.coffee__info {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.empty {
  width: 100px;
  height: 5px;
  background-color: white;
  color: white;
}

.img_machine {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.img_machine img {
  width: 100%;
}

.middle {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-direction: flex-start;
  align-items: flex-start;
}

@media screen and (max-width: 1000px) {
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: #ced4da;
    color: #f8f9fa;
    height: 300px;
  }
  .nav--bar {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  footer {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  footer .nav--bar--f {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .social {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .social h2 {
    font-size: 0.8rem;
  }
  .coffie {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    width: 100%;
  }
  .coffies-two {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    width: 100%;
  }
  .main--section--third {
    width: 100%;
  }
  .main--section {
    flex-direction: column;
    align-items: center;
  }
  .coffies {
    width: 100%;
    flex-direction: column;
  }
  .coffee__info {
    flex-direction: column;
  }
  .no-alc {
    flex-direction: column;
  }
  .h2main {
    margin: 0;
  }
  .openinghours {
    flex-direction: column;
  }
  .about_us_picture {
    img {
      width: 100%;
    }
  }
}

/*# sourceMappingURL=style.css.map */

.main--section .about_us {
  width: 80%;
  text-align: left;
  float: left;
}

.about_us p {
  text-align: left;
  margin: 20px;
}

.about_us_picture {
  display: block;
  width: 80%;
}

.form {
  display: block;
}
